<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()"></div>
        <div class="search">
          <form action="/">
            <van-search
              v-model="value"
              placeholder="搜索"
              show-action
              @search="onSearch"
              @cancel="onCancel"
            />
          </form>
        </div>
      </div>
    </header>
    <div class="quick-search">
      <div class="text">快速搜索聊天内容</div>
    </div>
    <div class="search-content">
      <van-row>
        <van-col span="8">日期</van-col>
        <van-col span="8">图片及视频</van-col>
        <van-col span="8">文件</van-col>
        <van-col span="8">链接</van-col>
        <van-col span="8">音乐</van-col>
        <van-col span="8">交易</van-col>
        <van-col span="8">小程序</van-col>
      </van-row>
    </div>
  </div>
</template>
<script>
import qs from "qs";
export default {
  data() {
    return {
      value: ""
    };
  },
  mounted() {},
  methods: {
    //搜索
    onSearch() {},
    //取消
    onCancel() {
      this.$router.back();
    }
  }
};
</script>
<style scoped>
.search {
  padding: 6px 0 6px 20px;
  height: 30px;
  border-radius: 5px;
}
.van-search__content {
  background: white;
}
.search .van-search {
  padding: 0;
}
.van-search .van-cell {
  padding: 5px 8px;
}
.text {
  font-size: 14px;
  padding-top: 35px;
  color: rgb(190, 187, 187);
  letter-spacing: 2px;
  text-align: center;
}
.search-content {
  padding: 30px 20px 0;
  text-align: center;
  font-size: 16px;
  color: #4a699c;
}
.search-content .van-col--8 {
  height: 40px;
}
</style>


